.navbar{
    background-color: #333;
    color: #fff;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    button{
        background-color: #fff;
        color: #333;
        padding: 5px 10px;
        border: none;
        border-radius: 5px;
        cursor: pointer;
    

    &:hover{
        background-color: #444;
    }
}

.logout{
    background-color: #fff;
    color: #333;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;

    &:hover{
        background-color: #eee;
    }
}
}
